import store from './store'

const host = process.env.NODE_ENV === 'development'
  ? 'localhost:8986'
  : 'api.nan.ci'

const init = () => {

  const ws = new WebSocket(`wss://${host}`)

  ws.addEventListener('open', () => {
    console.info('socket connection: OPENED')
  })

  ws.addEventListener('message', message => {
    const locations = JSON.parse(message.data)

    store.dispatch({ type: 'SET_LOCATIONS', locations })
  })

  ws.addEventListener('close', () => {
    console.info('socket connection: CLOSED')
    const ms = 1000 + 2000 * Math.random()

    console.info(`retrying to connect in ${ms} ms..`)
    setTimeout(init, ms)
  })

}

init()



